
.index_page {
  background: #f2f2f2;
}
.header_wrap {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 3;
}
.index_header {
  height: 1rem;
  background: #f2f2f2;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.index_header .logo {
  width: 1rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.index_header .logo img {
  width: 0.9rem;
  height: 0.9rem;
}
.index_header .search {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.index_header .search .search_box {
  width: 96%;
  margin: auto;
  height: 0.7rem;
  border: 1px solid #e5e5e5;
  border-radius: 0.1rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  background: white;
}
.index_header .search .search_box i {
  margin-left: 0.1rem;
  color: #dadada;
}
.index_header .search .search_box span {
  margin-left: 0.2rem;
  color: #999;
}
.theme .item {
  background: white;
}
.theme .item .theme_header {
  text-align: center;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.35rem;
  position: relative;
  color: #333;
  letter-spacing: 0.08rem;
  border-top: 0.15rem solid #eee;
  font-weight: 600;
  box-sizing: content-box;
}
.theme .item .theme_header span {
  position: relative;
}
.theme .item .theme_header span::after {
  right: -1rem;
}
.theme .item .theme_banner {
  width: 100%;
}
.theme .item .theme_banner img {
  width: 100%;
}
.theme .item .theme_goods {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.theme .item .theme_goods .t_goods {
  width: 50%;
  margin-bottom: 0.1rem;
}
.theme .item .theme_goods .t_goods .g_img {
  margin: 0.04rem;
  margin-top: 0.12rem;
  height: 3.67rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  overflow: hidden;
}
.theme .item .theme_goods .t_goods .g_img img {
  display: block;
  height: inherit;
}
.theme .item .theme_goods .t_goods .g_name {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 0.24rem;
  min-height: 0.8rem;
  padding: 0 0.2rem;
}
.theme .item .theme_goods .t_goods .g_price {
  padding-left: 0.2rem;
  height: 0.5rem;
  overflow: hidden;
}
.theme .item .theme_goods .t_goods .g_price .buy_price {
  color: #c00;
}
.theme .item .theme_goods .t_goods .g_price .market_price {
  text-decoration: line-through;
  margin-left: 0.5rem;
}
.theme .item .theme_goods .t_goods .g_price .y_h {
  background: #ff0036;
  color: white;
  font-size: 12px;
  border-radius: 4px;
  margin-right: 10px;
  padding: 0 4px;
  display: inline-block;
}
.jrdp .jrdp_header {
  font-size: 0.34rem;
  position: relative;
  text-indent: 0.2rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: white;
}
.jrdp .jrdp_header::after {
  position: absolute;
  content: "";
  width: 2px;
  background: #ff0036;
  height: 0.4rem;
  left: 0.01rem;
  top: 0.2rem;
}
.jrdp .jrdp_list .item {
  padding: 0 0.04rem;
}
.jrdp .jrdp_list .item img {
  width: 100%;
}
.tab_wrap {
  z-index: 2;
  height: 0.6rem;
  line-height: 0.6rem;
  background: #f2f2f2;
}
.tab_wrap .item {
  min-width: 40px;
  padding: 0 10px;
  text-align: center;
  display: inline-block;
}
.tab_wrap .active {
  color: #ff0036;
}
.tab_wrap .active span {
  border-bottom: 1px solid #ff0036;
  display: inline-block;
  line-height: 0.5rem;
}
.toutiao {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  height: 40px;
  background: white;
}
.toutiao span {
  line-height: 40px;
  font-size: 0.34rem;
  color: #ff0036;
  padding: 0 0.1rem;
  position: relative;
}
.toutiao span::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 20px;
  background: #ff0036;
  top: 10px;
  right: 0;
}
.toutiao .t_sw {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.toutiao .t_sw .t_item {
  padding: 0 0.15rem;
  height: 40px;
  line-height: 40px;
  font-size: 0.26rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.middle_banner {
  overflow: hidden;
}
.middle_banner .item {
  width: 3.7rem;
  height: 2.88rem;
  margin-bottom: 0.04rem;
  float: right;
}
.middle_banner .item img {
  width: 100%;
  height: 100%;
}
.middle_banner .item:first-of-type {
  float: left;
  height: 5.8rem;
}
.header_wrap div:nth-of-type(2) {
  background: #f2f2f2;
}
.vux-slider > .vux-swiper {
  height: 2.9rem !important;
}



