
.main {
  margin-top: 1rem;
  text-align: center;
}
.main .withdrawLists {
  border-bottom: 1px solid #ccc;
  text-align: left;
}
.main .withdrawLists .van-swipe-cell__right button {
  height: 100%;
}
.main .withdrawLists .van-swipe-cell__left button {
  height: 100%;
}
.add {
  border: 0;
  background: transparent;
  color: #6b6b6b;
  padding: 0 0.3rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.3rem;
  position: fixed;
  right: 0.3rem;
  top: 0.2rem;
  z-index: 100;
}



