
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.index_page {
  background: white;
}
.amountbox {
  margin-top: 1rem;
  text-align: center;
  font-size: 0.26rem;
}
.amountbox * {
  display: block;
}
.amountbox a {
  background: #f60;
  color: #fff;
  width: 6.8rem;
  height: 0.88rem;
  line-height: 0.88rem;
  margin: auto;
  margin-bottom: 0.25rem;
}
.amountbox p {
  color: #323232;
  font-size: 0.26rem;
  margin: 0rem auto 0.5rem;
}
.amountbox i {
  font-size: 0.6rem;
  color: #f60;
}
.amountbox span {
  margin: 0.5rem 0;
  font-size: 0.3rem;
}



