
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.index_page {
  background: white;
}
.couponnav {
  height: 0.88rem;
  line-height: 0.88rem;
  font-size: 0.26rem;
  color: #333;
  background: #fff;
  position: relative;
  z-index: 100;
  border-top: 1px solid #f4f4f4;
  overflow: scroll;
}
.couponnav div {
  width: 1.5rem;
  float: left;
  text-align: center;
}
.couponnav div span {
  display: inline-block;
  border-bottom: 0.02rem solid #fff;
}
.couponnav .active {
  color: #FF0036;
}
.couponnav .active span {
  border-bottom-color: #FF0036;
}
.cont {
  background: #F4F4F4;
}
.order {
  background: #fff;
  margin-top: 0.2rem;
  display: block;
}
.order p {
  height: 0.73rem;
  line-height: 0.73rem;
  color: #333;
  font-size: 0.26rem;
  padding: 0 0.3rem;
  background: #fff;
}
.order p span {
  color: #FF0036;
  float: right;
}
.order .ordergood {
  background: #fff;
  border-top: 1px solid #ddd;
  overflow: hidden;
  padding: 0.2rem 0.2rem 0.2rem 0.3rem;
}
.order .ordergood .pic {
  width: 1.25rem;
  height: 1.25rem;
  overflow: hidden;
  border: 1px solid #ddd;
  float: left;
  text-align: center;
  margin: 0;
}
.order .ordergood .pic img {
  height: 100%;
}
.order .ordergood .txt {
  float: left;
  width: 4.4rem;
  padding: 0 0 0 0.2rem;
  margin: 0;
}
.order .ordergood .txt h3 {
  font-size: 0.28rem;
  color: #333;
  line-height: 0.4rem;
  font-weight: normal;
  height: 0.8rem;
  overflow: hidden;
  margin-bottom: 0.1rem;
  text-align: justify;
}
.order .ordergood .txt h6 {
  font-size: 12px;
  color: #999;
  font-weight: normal;
  line-height: 0.3rem;
  height: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.order .ordergood .right {
  float: right;
  font-size: 12px;
  color: #333;
  text-align: right;
}
.order .ordergood .right del {
  display: block;
  color: #999;
}
.order .ordergood .right span {
  display: block;
  color: #999;
}
.order .total {
  color: #333;
  font-size: 0.26rem;
  text-align: right;
  border-top: 1px solid #ddd;
  height: 0.9rem;
  line-height: 0.9rem;
  padding: 0 0.3rem;
  background: #fff;
}
.order .btnbox {
  padding: 0.2rem;
  text-align: right;
}
.order .btnbox span,
.order .btnbox a {
  display: inline-block;
  height: 0.55rem;
  line-height: 0.55rem;
  padding: 0 0.15rem;
  font-size: 0.26rem;
  margin-left: 0.4rem;
  border: 1px solid #ddd;
  color: #555;
  border-radius: 0.1rem;
}
.order .btnbox span:nth-last-child(1) {
  color: #FF0036;
  border-color: #FF0036;
}
.nomore {
  color: #777;
  z-index: 100;
  text-align: center;
  font-size: 0.24rem;
  line-height: 0.4rem;
  margin: 0.3rem;
  position: relative;
}
.nomore:before,
.nomore:after {
  content: "";
  width: 30%;
  height: 1px;
  background: #999;
  display: inline-block;
  position: absolute;
  top: 50%;
}
.nomore:before {
  left: 0;
}
.nomore:after {
  right: 0;
}
.yo-scroll .pull-refresh {
  background: #f4f4f4;
  height: 1.7rem;
}
.nouse > div:nth-of-type(1) {
  z-index: 100;
}



