
.select {
  bottom: 0rem;
  position: fixed;
  width: 100%;
  border-top: 1px solid #e0e0e0;
  z-index: 999999;
}
.select div {
  float: left;
  width: 50%;
  text-align: center;
  line-height: 0.9rem;
  font-size: 0.38rem;
}
.selectactive {
  color: green;
}
.van-cell__value span {
  word-break: break-all;
  word-wrap: break-word;
}
.mainc {
  margin-top: 0;
  text-align: center;
}
.mainc .flowLists {
  border-bottom: 1px solid #ccc;
  text-align: left;
}
.mainc .flowLists .van-swipe-cell__right button {
  height: 100%;
}
.mainc .flowLists .van-swipe-cell__left button {
  height: 100%;
}
.add {
  border: 0;
  background: transparent;
  color: #6b6b6b;
  padding: 0 0.3rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.3rem;
  position: fixed;
  right: 0.3rem;
  top: 0.2rem;
  z-index: 100;
}



