
.transfer {
  background: #f60;
  color: #fff;
  width: 90%;
  line-height: 1rem;
  border: 0;
  border-radius: 0.2rem;
  margin: 1rem 5%;
}
.main {
  margin-top: 1rem;
  text-align: center;
  text-align: left;
}
.main .line {
  height: 0.12rem;
  background: -webkit-repeating-linear-gradient(135deg, #8fc9f5 0, #8fc9f5 50%, #f58f8f 50%, #f58f8f 100%);
  background: repeating-linear-gradient(-45deg, #8fc9f5 0, #8fc9f5 50%, #f58f8f 50%, #f58f8f 100%);
  margin-bottom: 0.5rem;
}
.main .nochoose {
  line-height: 1rem;
  text-align: center;
}
.add {
  border: 0;
  background: transparent;
  color: #6b6b6b;
  padding: 0 0.3rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.3rem;
  position: fixed;
  right: 0.3rem;
  top: 0.2rem;
  z-index: 100;
}



