
.center_page {
  background: white;
  padding-bottom: 0.98rem;
  height: auto !important;
}
.info {
  background: -webkit-linear-gradient(left, #ffa503, #ff0036);
  /* Safari 5.1 - 6.0 */
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: linear-gradient(to right, #ffa503, #ff0036);
  /* 标准的语法 */
  display: block;
  height: 3.2rem;
  padding: 0.7rem 0.3rem;
  box-sizing: border-box;
  overflow: hidden;
}
.info .pic {
  height: 1.4rem;
  width: 1.4rem;
  border-radius: 50%;
  overflow: hidden;
  float: left;
  background: #e5cc63;
  position: relative;
}
.info .pic span {
  display: block;
  text-align: center;
  margin: 0;
  font-size: 0.3rem;
  color: #ffefde;
}
.info .pic img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
}
.info span {
  margin-left: 0.2rem;
  color: #ffefde;
  line-height: 0.6rem;
}
.info span:nth-of-type(2) {
  color: rgba(255, 255, 255, 0.6);
}
.info span:nth-of-type(4) {
  float: right;
  font-size: 0.4rem;
  line-height: 0rem;
}
.myorder {
  background: #fff;
  color: #323232;
}
.myorder .p {
  text-align: left;
  font-size: 0.26rem;
  height: 0.88rem;
  line-height: 0.88rem;
  padding: 0 0.3rem;
  border-bottom: 1px solid #dddddd;
  display: block;
}
.myorder .p span {
  float: right;
  color: #323232;
}
.myorder p {
  font-size: 0.26rem;
  height: 0.88rem;
  line-height: 0.88rem;
  padding: 0 0.3rem;
  border-bottom: 1px solid #dddddd;
}
.myorder p span {
  float: right;
  color: #323232;
}
.myorder div {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 1.86rem;
  box-sizing: border-box;
  padding: 0.25rem 0;
  border-bottom: 0.16rem solid #eee;
}
.myorder div a,
.myorder div kbd {
  display: inline-block;
  text-align: center;
  position: relative;
  font-style: normal;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
.myorder div a i,
.myorder div kbd i {
  display: block;
  font-size: 0.45rem;
  height: 0.7rem;
  line-height: 0.7rem;
}
.myorder div a span,
.myorder div kbd span {
  display: inline-block;
  background: #d21018;
  font-size: 12px;
  color: #fff;
  border-radius: 50%;
  position: absolute;
  right: 3px;
  top: 0;
  width: 0.35rem;
  height: 0.35rem;
  line-height: 0.35rem;
}
.myorder2 {
  overflow: hidden;
}
.myorder2 p {
  border-bottom: 0;
}
.myorder2 div {
  border-bottom: 0;
  padding: 0.5rem 0 0.3rem;
  height: auto;
  border-top: 1px solid #dadada;
  display: block;
}
.myorder2 a,
.myorder2 kbd {
  width: 33.3%;
  height: 1.8rem;
  float: left;
  color: #949494;
}
.myorder2 a i,
.myorder2 kbd i {
  color: #ff0036;
}



